import './styles/globals.scss';
import React from 'react';
import { GlobalStoreProvider, useGlobalStore } from './context/GlobalStore';
import { getTileKey } from './utils/utils';
import Board from './components/Board/Board';
import Footer from './components/Footer/Footer';
import Results from './components/Results/Results';
import { wordleAnswersList } from './utils/utils';
const WordleHelper = () => {
    const { globalStore, setGlobalStore } = useGlobalStore();
    const getPossibleWords = ({ wordleAnswersList, solutionState, }) => {
        // Input validation
        if (!wordleAnswersList?.length || !solutionState) {
            return [];
        }
        const { exclude, include, letters } = solutionState;
        // Normalize input
        const normalizedInclude = (include || []).map(letter => letter.toLowerCase());
        const normalizedExclude = (exclude || []).map(letter => letter.toLowerCase());
        const includesLetter = (word) => normalizedInclude.every(letter => word.includes(letter));
        const excludesLetter = (word) => !normalizedExclude.some(letter => word.includes(letter));
        try {
            const letterPatterns = Object.entries(letters).map(([position, { is, isnot }]) => {
                const pattern = new RegExp(`^.{${Number(position) - 1}}${is
                    ? is.toLowerCase()
                    : isnot?.length > 0
                        ? `[^${isnot.join('').toLowerCase()}]`
                        : '.'}.{${5 - Number(position)}}$`, 'i');
                return { pattern, position };
            });
            return wordleAnswersList.filter(word => {
                if (!word || typeof word !== 'string')
                    return false;
                if (!excludesLetter(word))
                    return false;
                if (!includesLetter(word))
                    return false;
                return letterPatterns.every(({ pattern }) => pattern.test(word));
            });
        }
        catch (error) {
            console.error('Error in getPossibleWords:', error);
            return [];
        }
    };
    const handleSearch = () => {
        const solutionState = extractSolutionState(globalStore.boardState);
        const possibleWords = getPossibleWords({
            wordleAnswersList,
            solutionState,
        });
        setGlobalStore(prevState => ({
            ...prevState,
            results: possibleWords, // Update results in the global store
        }));
    };
    const handleClearBoard = () => {
        const initialState = {};
        for (let row = 1; row <= 6; row++) {
            for (let col = 1; col <= 5; col++) {
                const key = getTileKey(row, col);
                initialState[key] = { value: '', state: '' };
            }
        }
        setGlobalStore(prev => ({
            ...prev,
            boardState: initialState,
            results: [],
            focusedTile: { row: 1, col: 1 },
        }));
    };
    return (React.createElement("main", { className: 'wordle-helper' },
        React.createElement("h1", { className: 'visually-hidden' }, "Wordle Helper"),
        React.createElement(Board, null),
        React.createElement("div", { className: 'button-container' },
            React.createElement("button", { "aria-label": 'Clear board and start over', className: 'clear-button', onClick: handleClearBoard }, "Clear Board"),
            React.createElement("button", { "aria-label": 'Search for possible words', className: 'search-button', onClick: handleSearch }, "Find Solutions")),
        React.createElement(Results, null),
        React.createElement(Footer, null)));
};
const extractSolutionState = (boardState) => {
    const solutionState = {
        exclude: [],
        include: [],
        letters: {
            1: { is: null, isnot: [] },
            2: { is: null, isnot: [] },
            3: { is: null, isnot: [] },
            4: { is: null, isnot: [] },
            5: { is: null, isnot: [] },
        },
    };
    Object.entries(boardState).forEach(([key, { value, state }]) => {
        const position = parseInt(key.split('-')[1], 10);
        if (value) {
            if (state === 'correct') {
                solutionState.letters[position].is = value;
                if (!solutionState.include.includes(value)) {
                    solutionState.include.push(value);
                }
            }
            else if (state === 'present') {
                if (!solutionState.include.includes(value)) {
                    solutionState.include.push(value);
                }
                solutionState.letters[position].isnot.push(value);
            }
            else if (state === 'absent') {
                if (!solutionState.exclude.includes(value)) {
                    solutionState.exclude.push(value);
                }
            }
        }
    });
    return solutionState;
};
