import React, { createContext, useContext, useState } from 'react';
export const GlobalStoreContext = createContext(undefined);
export const useGlobalStore = () => {
    const context = useContext(GlobalStoreContext);
    if (!context) {
        throw new Error('useGlobalStore must be used within a GlobalStoreProvider');
    }
    return context;
};
const createInitialBoardState = () => {
    const boardState = {};
    for (let row = 1; row <= 6; row++) {
        for (let col = 1; col <= 5; col++) {
            boardState[`${row}-${col}`] = { value: '', state: '' };
        }
    }
    return boardState;
};
export const GlobalStoreProvider = ({ children, }) => {
    const [globalStore, setGlobalStore] = useState({
        solutionState: {
            exclude: [],
            include: [],
            letters: {
                1: { is: null, isnot: [] },
                2: { is: null, isnot: [] },
                3: { is: null, isnot: [] },
                4: { is: null, isnot: [] },
                5: { is: null, isnot: [] },
            },
        },
        boardState: createInitialBoardState(),
        focusedTile: null,
        results: [],
    });
    return (React.createElement(GlobalStoreContext.Provider, { value: { globalStore, setGlobalStore } }, children));
};
